"use strict";
import {Promise} from "q";
import {IEventTarget} from "../common/event";
import {Point} from "../common/point";
import {PositionInfo} from "../common/positionInfo";

export interface IContextMenuProvider extends IEventTarget {
    callback(id: string): Promise<any>;
    dismissAll(): Promise<any>;
    dismissCurrent(ignoreDismissForRoot: boolean): Promise<any>;
    dismissSubmenus(currentCoordinates: Point): Promise<any>;
    contentready(): Promise<any>;
    show(id: string, ariaLabel: string, contextMenuHtml: string, positionInfo: PositionInfo): Promise<any>;
}
